import useResetPasswordMail from 'hooks/useResetPasswordMail';

import { Formik, Form } from 'formik';
import EntryField from 'components/InterfaceElements/EntryField';
import Button from 'components/InterfaceElements/Button';

import { resetPasswordSchema } from 'helpers/validationSchemas/resetPasswordSchema';
import { Text } from './ResetPassword.styled';

const ResetPassword: React.FC<{}> = () => {
  const { initialValues, onSubmit } = useResetPasswordMail();

  return (
    <>
      <Text>
        Enter the email address to which the account is registered. We will send
        an email to reset and reset your password.
      </Text>

      <Formik
        initialValues={initialValues}
        onSubmit={onSubmit}
        validationSchema={resetPasswordSchema}
      >
        {({ values, handleSubmit, handleChange }) => (
          <Form onSubmit={handleSubmit}>
            <EntryField
              label="email"
              type="email"
              name="email"
              value={values.email}
              onChange={handleChange}
              placeholder="Enter your e-mail"
              required={true}
              height="50px"
              width="100%"
              margin="0 0 30px 0"
            />

            <Button
              type="submit"
              background="green"
              width="100%"
              height="60px"
              borderRadius="50px"
              shadow
            >
              send
            </Button>
          </Form>
        )}
      </Formik>
    </>
  );
};

export default ResetPassword;
